.gpt3__cta {
    border: 0px solid white;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;

    border-radius: 1rem;
    background: var(--gradient-bar)
}

.gpt3__cta_content {
    flex: 1;
}

.gpt3__cta_content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;

    color: #0E0E0E;
}

.gpt3__cta_content h3 {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;


    color: #000000;
}

.gpt3__cta_button {
    display: flex;
}

.gpt3__cta_button button {
    /* position: absolute; */
    width: 189.14px;
    height: 59.4px;


    background: #000000;
    border-radius: 40px;
    cursor: pointer;

    font-family: var(--font-family);
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;


    color: #FFFFFF;
}

@media screen and (max-width:650px)
{
    .gpt3__cta{
        flex-direction: column;
    }

    .gpt3__cta_content h3{
        margin-bottom: 1rem;
    }
}

@media screen and (max-width:550px)
{
    .gpt3__cta{
        margin: 4rem 2rem;
    }

    .gpt3__cta_content h3{
        font-size:18px;
        line-height: 32px;
    }

    .gpt3__cta_button button{
        font-size: 18px;
        line-height: 32px;
    }
}