.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility_img {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.gpt3__possibility_img img {
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 2rem;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    /* identical to box height, or 188% */
    color: #71E5FF;

    margin-bottom: 1.5rem;
}

.gpt3__possibility-content h4:last-child{
    color: #FF8A71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: #81AFDD;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

@media screen and (max-width:1015px)
{
    .gpt3__possibility{
        flex-direction: column;
    }

    .gpt3__possibility-content h4{
        margin-top: 2rem;
    }
}